@import url(https://fonts.googleapis.com/css?family=Noto%20Sans:400,700);

body {
  background: #fbede7;
  font-family: 'Noto Sans', sans-serif;
  line-height: 1.5;
}

.content {
  background: #fff;
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
  max-width: 400px;
  margin: 4em auto;
  border-radius: 8px;
  overflow: hidden;
}

.conversation-title {
  text-align: center;
  padding: 10px;
  background: #31a4d9;
  color: #fff;
}
.conversation-title h1 {
  font: 700 18px 'Noto Sans', sans-serif;
  margin: 0;
}
.conversation-title .status {
  font-size: 15px;
}

.conversation-wrapper {
  background: #f9f9f9;
  padding: 25px;
  min-height: 300px;
  overflow-y: scroll;
}

.message-sent, .message-received {
  padding: 0;
}

.message-received {
  text-align: left;
}

.message-sent {
  text-align: right;
}

.bubble {
  border-radius: 8px;
  background: #fff;
  padding: 12px 18px;
  border: 0.5px solid rgba(0, 0, 0, 0.02);
  -webkit-box-shadow: 0 2px 1px rgba(0, 0, 0, 0.08);
          box-shadow: 0 2px 1px rgba(0, 0, 0, 0.08);
  max-width: 70%;
  display: inline-block;
  text-align: left;
}

.timestamp {
  margin: 8px 0;
}
.timestamp .avatar {
  border-radius: 50%;
  background: -webkit-gradient(linear, left bottom, right top, from(#fc4a1a), to(#f7b733));
  background: -webkit-linear-gradient(bottom left, #fc4a1a, #f7b733);
  background: -o-linear-gradient(bottom left, #fc4a1a, #f7b733);
  background: linear-gradient(to top right, #fc4a1a, #f7b733);
  display: inline-block;
  width: 25px;
  height: 20px;
  vertical-align: top;
  color: #fff;
  font-size: 10px;
  text-align: center;
  padding-top: 5px;
}
.timestamp .time {
  display: inline-block;
  font-size: 15px;
  color: #999;
  margin: 0 5px;
}

.messaging-area {
  position: relative;
  -webkit-box-shadow: 0 -2px 1px rgba(0, 0, 0, 0.03);
          box-shadow: 0 -2px 1px rgba(0, 0, 0, 0.03);
}
.messaging-area input {
  font: 16px 'Noto Sans', sans-serif;
  border: none;
  padding: 20px 30px;
  width: 100%;
}
.messaging-area input:focus {
  outline: 0;
}
.messaging-area button.send {
  background: #31a4d9;
  color: #fff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  padding: 13px 10px 20px;
  position: absolute;
  font-size: 1.4em;
  text-align: center;
  right: 8px;
  bottom: 8px;
}


